<template>
  <div class="addEdit-block">
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
      :style="{ backgroundColor: addEditForm.addEditBoxColor }"
      :inline="true"
    >
      <el-row>
        <el-col :span="12">
          <el-form-item
            class="input"
            v-if="type != 'info'"
            label="用户名"
            prop="yonghuming"
          >
            <el-input
              v-model="ruleForm.yonghuming"
              placeholder="用户名"
              clearable
              :readonly="ro.yonghuming"
            ></el-input>
          </el-form-item>
          <div v-else>
            <el-form-item class="input" label="用户名" prop="yonghuming">
              <el-input
                v-model="ruleForm.yonghuming"
                placeholder="用户名"
                readonly
              ></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="12">
          <el-form-item
            class="input"
            v-if="type != 'info'"
            label="密码"
            prop="mima"
          >
            <el-input
              v-model="ruleForm.mima"
              placeholder="密码"
              clearable
              :readonly="ro.mima"
            ></el-input>
          </el-form-item>
          <div v-else>
            <el-form-item class="input" label="密码" prop="mima">
              <el-input
                v-model="ruleForm.mima"
                placeholder="密码"
                readonly
              ></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="12">
          <el-form-item
            class="input"
            v-if="type != 'info'"
            label="姓名"
            prop="xingming"
          >
            <el-input
              v-model="ruleForm.xingming"
              placeholder="姓名"
              clearable
              :readonly="ro.xingming"
            ></el-input>
          </el-form-item>
          <div v-else>
            <el-form-item class="input" label="姓名" prop="xingming">
              <el-input
                v-model="ruleForm.xingming"
                placeholder="姓名"
                readonly
              ></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="12">
          <el-form-item
            class="select"
            v-if="type != 'info'"
            label="性别"
            prop="xingbie"
          >
            <el-select
              :disabled="ro.xingbie"
              v-model="ruleForm.xingbie"
              placeholder="请选择性别"
            >
              <el-option
                v-for="(item, index) in xingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <div v-else>
            <el-form-item class="input" label="性别" prop="xingbie">
              <el-input
                v-model="ruleForm.xingbie"
                placeholder="性别"
                readonly
              ></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="24">
          <el-form-item
            class="upload"
            v-if="type != 'info' && !ro.touxiang"
            label="头像"
            prop="touxiang"
          >
            <file-upload
              tip="点击上传头像"
              action="file/upload"
              :limit="3"
              :multiple="true"
              :fileUrls="ruleForm.touxiang ? ruleForm.touxiang : ''"
              @change="touxiangUploadChange"
            ></file-upload>
          </el-form-item>
          <div v-else>
            <el-form-item v-if="ruleForm.touxiang" label="头像" prop="touxiang">
              <img
                style="margin-right: 20px"
                v-bind:key="index"
                v-for="(item, index) in ruleForm.touxiang.split(',')"
                :src="$base.url + item"
                width="100"
                height="100"
              />
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="12">
          <el-form-item
            class="input"
            v-if="type != 'info'"
            label="手机"
            prop="shouji"
          >
            <el-input
              v-model="ruleForm.shouji"
              placeholder="手机"
              clearable
              :readonly="ro.shouji"
            ></el-input>
          </el-form-item>
          <div v-else>
            <el-form-item class="input" label="手机" prop="shouji">
              <el-input
                v-model="ruleForm.shouji"
                placeholder="手机"
                readonly
              ></el-input>
            </el-form-item>
          </div>
        </el-col>
        <!-- <el-col :span="12">
        <el-form-item class="input" v-if="type!='info'"  label="住址" prop="zhuzhi">
          <el-input v-model="ruleForm.zhuzhi" 
              placeholder="住址" clearable  :readonly="ro.zhuzhi"></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item class="input" label="住址" prop="zhuzhi">
              <el-input v-model="ruleForm.zhuzhi" 
                placeholder="住址" readonly></el-input>
          </el-form-item>
        </div>
      </el-col> -->
      </el-row>
      <el-row>
        <el-form-item label="住址区域">
          <el-select v-model="areaId" placeholder="请选择" @change="areaChangeHandle">
            <el-option
              v-for="item in areaList"
              :key="item.id"
              :label="item.areaName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item> 
        <el-form-item label="住址楼栋">
          <el-select v-model="floorId" placeholder="请选择" @change="floorChangeHandle">
            <el-option
              v-for="item in floorList"
              :key="item.id"
              :label="item.floorNum"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item> 
        <el-form-item label="住址楼栋">
          <el-select v-model="roomId" placeholder="请选择" @change="floorChangeHandle">
            <el-option
              v-for="item in roomList"
              :key="item.id"
              :label="item.roomNum"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item> 
      </el-row>
      <el-form-item class="btn">
        <el-button
          v-if="type != 'info'"
          type="primary"
          class="btn-success"
          @click="onSubmit"
          >提交</el-button
        >
        <el-button v-if="type != 'info'" class="btn-close" @click="back()"
          >取消</el-button
        >
        <el-button v-if="type == 'info'" class="btn-close" @click="back()"
          >返回</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// 数字，邮件，手机，url，身份证校验
import {
  isNumber,
  isIntNumer,
  isEmail,
  isPhone,
  isMobile,
  isURL,
  checkIdCard,
} from "@/utils/validate";
export default {
  data() {
    let self = this;
    var validateIdCard = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!checkIdCard(value)) {
        callback(new Error("请输入正确的身份证号码"));
      } else {
        callback();
      }
    };
    var validateUrl = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isURL(value)) {
        callback(new Error("请输入正确的URL地址"));
      } else {
        callback();
      }
    };
    var validateMobile = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isMobile(value)) {
        callback(new Error("请输入正确的手机号码"));
      } else {
        callback();
      }
    };
    var validatePhone = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isPhone(value)) {
        callback(new Error("请输入正确的电话号码"));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isEmail(value)) {
        callback(new Error("请输入正确的邮箱地址"));
      } else {
        callback();
      }
    };
    var validateNumber = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isNumber(value)) {
        callback(new Error("请输入数字"));
      } else {
        callback();
      }
    };
    var validateIntNumber = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isIntNumer(value)) {
        callback(new Error("请输入整数"));
      } else {
        callback();
      }
    };
    return {
      addEditForm: {
        btnSaveFontColor: "#fff",
        selectFontSize: "14px",
        btnCancelBorderColor: "rgba(12, 107, 92, 1)",
        inputBorderRadius: "4px",
        inputFontSize: "14px",
        textareaBgColor: "#fff",
        btnSaveFontSize: "14px",
        textareaBorderRadius: "4px",
        uploadBgColor: "#fff",
        textareaBorderStyle: "solid",
        btnCancelWidth: "88px",
        textareaHeight: "120px",
        dateBgColor: "#fff",
        btnSaveBorderRadius: "4px",
        uploadLableFontSize: "14px",
        textareaBorderWidth: "1px",
        inputLableColor: "#606266",
        addEditBoxColor: "rgba(255, 255, 255, 1)",
        dateIconFontSize: "14px",
        btnSaveBgColor: "rgba(12, 107, 92, 1)",
        uploadIconFontColor: "#8c939d",
        textareaBorderColor: "#DCDFE6",
        btnCancelBgColor: "rgba(255, 255, 255, 1)",
        selectLableColor: "#606266",
        btnSaveBorderStyle: "dashed ",
        dateBorderWidth: "1px",
        dateLableFontSize: "14px",
        dateBorderRadius: "4px",
        btnCancelBorderStyle: "dashed ",
        selectLableFontSize: "14px",
        selectBorderStyle: "solid",
        selectIconFontColor: "#C0C4CC",
        btnCancelHeight: "44px",
        inputHeight: "40px",
        btnCancelFontColor: "#606266",
        dateBorderColor: "#DCDFE6",
        dateIconFontColor: "#C0C4CC",
        uploadBorderStyle: "solid",
        dateBorderStyle: "solid",
        dateLableColor: "#606266",
        dateFontSize: "14px",
        inputBorderWidth: "1px",
        uploadIconFontSize: "28px",
        selectHeight: "40px",
        inputFontColor: "#606266",
        uploadHeight: "148px",
        textareaLableColor: "#606266",
        textareaLableFontSize: "14px",
        btnCancelFontSize: "14px",
        inputBorderStyle: "solid",
        btnCancelBorderRadius: "4px",
        inputBgColor: "#fff",
        inputLableFontSize: "14px",
        uploadLableColor: "#606266",
        uploadBorderRadius: "4px",
        btnSaveHeight: "44px",
        selectBgColor: "#fff",
        btnSaveWidth: "88px",
        selectIconFontSize: "14px",
        dateHeight: "40px",
        selectBorderColor: "#DCDFE6",
        inputBorderColor: "#DCDFE6",
        uploadBorderColor: "#DCDFE6",
        textareaFontColor: "#606266",
        selectBorderWidth: "1px",
        dateFontColor: "#606266",
        btnCancelBorderWidth: "2px",
        uploadBorderWidth: "1px",
        textareaFontSize: "14px",
        selectBorderRadius: "4px",
        selectFontColor: "#606266",
        btnSaveBorderColor: "rgba(255, 255, 255, 1)",
        btnSaveBorderWidth: "2px",
      },
      id: "",
      type: "",
      ro: {
        yonghuming: false,
        mima: false,
        xingming: false,
        xingbie: false,
        touxiang: false,
        shouji: false,
        zhuzhi: false,
      },
      ruleForm: {
        yonghuming: "",
        mima: "",
        xingming: "",
        xingbie: "",
        touxiang: "",
        shouji: "",
        zhuzhi: "",
      },
      xingbieOptions: [],
      rules: {
        yonghuming: [
          { required: true, message: "用户名不能为空", trigger: "blur" },
        ],
        mima: [{ required: true, message: "密码不能为空", trigger: "blur" }],
        xingming: [
          { required: true, message: "姓名不能为空", trigger: "blur" },
        ],
        xingbie: [],
        touxiang: [],
        shouji: [{ validator: validateMobile, trigger: "blur" }],
        zhuzhi: [],
      },
      // 
      areaId: null,
      areaList: [],
      floorId: null,
      floorList: [],
      roomId: null,
      roomList: [],
      yonghuAddrId: null
    };
  },
  props: ["parent"],
  computed: {},
  created() {
    this.addEditStyleChange();
    this.addEditUploadStyleChange();
  },
  mounted() {
    this.getAreaList()
  },
  methods: {
    // 下载
    download(file) {
      window.open(`${file}`);
    },
    // 初始化
    init(id, type) {
      if (id) {
        this.id = id;
        this.type = type;
      }
      if (this.type == "info" || this.type == "else") {
        this.info(id);
      } else if (this.type == "logistics") {
        this.logistics = false;
        this.info(id);
      } else if (this.type == "cross") {
        var obj = this.$storage.getObj("crossObj");
        for (var o in obj) {
          if (o == "yonghuming") {
            this.ruleForm.yonghuming = obj[o];
            this.ro.yonghuming = true;
            continue;
          }
          if (o == "mima") {
            this.ruleForm.mima = obj[o];
            this.ro.mima = true;
            continue;
          }
          if (o == "xingming") {
            this.ruleForm.xingming = obj[o];
            this.ro.xingming = true;
            continue;
          }
          if (o == "xingbie") {
            this.ruleForm.xingbie = obj[o];
            this.ro.xingbie = true;
            continue;
          }
          if (o == "touxiang") {
            this.ruleForm.touxiang = obj[o];
            this.ro.touxiang = true;
            continue;
          }
          if (o == "shouji") {
            this.ruleForm.shouji = obj[o];
            this.ro.shouji = true;
            continue;
          }
          if (o == "zhuzhi") {
            this.ruleForm.zhuzhi = obj[o];
            this.ro.zhuzhi = true;
            continue;
          }
        }
      }
      // 获取用户信息
      this.$http({
        url: `${this.$storage.get("sessionTable")}/session`,
        method: "get",
      }).then(({ data }) => {
        if (data && data.code === 0) {
          var json = data.data;
        } else {
          this.$message.error(data.msg);
        }
      });
      this.xingbieOptions = "男,女".split(",");
    },
    // 多级联动参数
    info(id) {
      let that = this
      this.$http({
        url: `yonghu/info/${id}`,
        method: "get",
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.ruleForm = data.data;
          //解决前台上传图片后台不显示的问题
          let reg = new RegExp("../../../upload", "g"); //g代表全部
          // 得到区域信息
          this.$http({
            url: '/addr/one/'+data.data.id,
            method: 'get',
          }).then(({data:res})=>{
            if(res.code != 0) return console.log('获取地址失败')
            this.yonghuAddrId = res.data[0].id
            this.areaId = parseInt(res.data[0].areaId)
            this.areaChangeHandle(res.data[0].areaId)
            this.floorId = parseInt(res.data[0].floorId)
            this.floorChangeHandle(res.data[0].floorId)
            this.roomId = parseInt(res.data[0].roomId)

          })

        } else {
          this.$message.error(data.msg);
        }
      });
    },

    // 提交
    onSubmit() {
      if (this.ruleForm.touxiang != null) {
        this.ruleForm.touxiang = this.ruleForm.touxiang.replace(
          new RegExp(this.$base.url, "g"),
          ""
        );
      }

      var objcross = this.$storage.getObj("crossObj");

      //更新跨表属性
      var crossuserid;
      var crossrefid;
      var crossoptnum;
      if (this.type == "cross") {
        var statusColumnName = this.$storage.get("statusColumnName");
        var statusColumnValue = this.$storage.get("statusColumnValue");
        if (statusColumnName != "") {
          var obj = this.$storage.getObj("crossObj");
          if (!statusColumnName.startsWith("[")) {
            for (var o in obj) {
              if (o == statusColumnName) {
                obj[o] = statusColumnValue;
              }
            }
            var table = this.$storage.get("crossTable");
            this.$http({
              url: `${table}/update`,
              method: "post",
              data: obj,
            }).then(({ data }) => {});
          } else {
            crossuserid = this.$storage.get("userid");
            crossrefid = obj["id"];
            crossoptnum = this.$storage.get("statusColumnName");
            crossoptnum = crossoptnum.replace(/\[/, "").replace(/\]/, "");
          }
        }
      }
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          if (crossrefid && crossuserid) {
            this.ruleForm.crossuserid = crossuserid;
            this.ruleForm.crossrefid = crossrefid;
            let params = {
              page: 1,
              limit: 10,
              crossuserid: this.ruleForm.crossuserid,
              crossrefid: this.ruleForm.crossrefid,
            };
            this.$http({
              url: "yonghu/page",
              method: "get",
              params: params,
            }).then(({ data }) => {
              if (data && data.code === 0) {
                if (data.data.total >= crossoptnum) {
                  this.$message.error(this.$storage.get("tips"));
                  return false;
                } else {
                  this.$http({
                    url: `yonghu/${!this.ruleForm.id ? "save" : "update"}`,
                    method: "post",
                    data: this.ruleForm,
                  }).then(({ data }) => {
                    if (data && data.code === 0) {

                      this.$message({
                        message: "操作成功",
                        type: "success",
                        duration: 1500,
                        onClose: () => {
                          this.parent.showFlag = true;
                          this.parent.addOrUpdateFlag = false;
                          this.parent.yonghuCrossAddOrUpdateFlag = false;
                          this.parent.search();
                          this.parent.contentStyleChange();
                        },
                      });
                    } else {
                      this.$message.error(data.msg);
                    }
                  });
                }
              } else {
              }
            });
          } else {
            this.$http({
              url: `yonghu/${!this.ruleForm.id ? "save" : "update"}`,
              method: "post",
              data: this.ruleForm,
            }).then(({ data }) => {
              if (data && data.code === 0) {
                let addrData = {
                  id: this.yonghuAddrId?this.yonghuAddrId:null,
                  areaId: this.areaId,
                  floorId: this.floorId,
                  roomId: this.roomId
                }
                let url = '/addr/save'
                if(this.ruleForm.id){
                  url = '/addr/update'
                  addrData.yonghuId = this.ruleForm.id
                }else {
                  url = '/addr/save'
                  addrData.yonghuId = data.data.id
                }
                  // 保存地址信息
                  this.$http({
                    url,
                    method: 'post',
                    data: addrData
                  }).then(({ data: res }) => {
                    console.log(res)
                  })
                this.$message({
                  message: "操作成功",
                  type: "success",
                  duration: 1500,
                  onClose: () => {
                    this.parent.showFlag = true;
                    this.parent.addOrUpdateFlag = false;
                    this.parent.yonghuCrossAddOrUpdateFlag = false;
                    this.parent.search();
                    this.parent.contentStyleChange();
                  },
                });
              } else {
                this.$message.error(data.msg);
              }
            });
          }
        }
      });
    
    
    },
    // 获取uuid
    getUUID() {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.yonghuCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    touxiangUploadChange(fileUrls) {
      this.ruleForm.touxiang = fileUrls;
      this.addEditUploadStyleChange();
    },
    addEditStyleChange() {
      this.$nextTick(() => {
        // input
        document
          .querySelectorAll(".addEdit-block .input .el-input__inner")
          .forEach((el) => {
            el.style.height = this.addEditForm.inputHeight;
            el.style.color = this.addEditForm.inputFontColor;
            el.style.fontSize = this.addEditForm.inputFontSize;
            el.style.borderWidth = this.addEditForm.inputBorderWidth;
            el.style.borderStyle = this.addEditForm.inputBorderStyle;
            el.style.borderColor = this.addEditForm.inputBorderColor;
            el.style.borderRadius = this.addEditForm.inputBorderRadius;
            el.style.backgroundColor = this.addEditForm.inputBgColor;
          });
        document
          .querySelectorAll(".addEdit-block .input .el-form-item__label")
          .forEach((el) => {
            el.style.lineHeight = this.addEditForm.inputHeight;
            el.style.color = this.addEditForm.inputLableColor;
            el.style.fontSize = this.addEditForm.inputLableFontSize;
          });
        // select
        document
          .querySelectorAll(".addEdit-block .select .el-input__inner")
          .forEach((el) => {
            el.style.height = this.addEditForm.selectHeight;
            el.style.color = this.addEditForm.selectFontColor;
            el.style.fontSize = this.addEditForm.selectFontSize;
            el.style.borderWidth = this.addEditForm.selectBorderWidth;
            el.style.borderStyle = this.addEditForm.selectBorderStyle;
            el.style.borderColor = this.addEditForm.selectBorderColor;
            el.style.borderRadius = this.addEditForm.selectBorderRadius;
            el.style.backgroundColor = this.addEditForm.selectBgColor;
          });
        document
          .querySelectorAll(".addEdit-block .select .el-form-item__label")
          .forEach((el) => {
            el.style.lineHeight = this.addEditForm.selectHeight;
            el.style.color = this.addEditForm.selectLableColor;
            el.style.fontSize = this.addEditForm.selectLableFontSize;
          });
        document
          .querySelectorAll(".addEdit-block .select .el-select__caret")
          .forEach((el) => {
            el.style.color = this.addEditForm.selectIconFontColor;
            el.style.fontSize = this.addEditForm.selectIconFontSize;
          });
        // date
        document
          .querySelectorAll(".addEdit-block .date .el-input__inner")
          .forEach((el) => {
            el.style.height = this.addEditForm.dateHeight;
            el.style.color = this.addEditForm.dateFontColor;
            el.style.fontSize = this.addEditForm.dateFontSize;
            el.style.borderWidth = this.addEditForm.dateBorderWidth;
            el.style.borderStyle = this.addEditForm.dateBorderStyle;
            el.style.borderColor = this.addEditForm.dateBorderColor;
            el.style.borderRadius = this.addEditForm.dateBorderRadius;
            el.style.backgroundColor = this.addEditForm.dateBgColor;
          });
        document
          .querySelectorAll(".addEdit-block .date .el-form-item__label")
          .forEach((el) => {
            el.style.lineHeight = this.addEditForm.dateHeight;
            el.style.color = this.addEditForm.dateLableColor;
            el.style.fontSize = this.addEditForm.dateLableFontSize;
          });
        document
          .querySelectorAll(".addEdit-block .date .el-input__icon")
          .forEach((el) => {
            el.style.color = this.addEditForm.dateIconFontColor;
            el.style.fontSize = this.addEditForm.dateIconFontSize;
            el.style.lineHeight = this.addEditForm.dateHeight;
          });
        // upload
        let iconLineHeight =
          parseInt(this.addEditForm.uploadHeight) -
          parseInt(this.addEditForm.uploadBorderWidth) * 2 +
          "px";
        document
          .querySelectorAll(".addEdit-block .upload .el-upload--picture-card")
          .forEach((el) => {
            el.style.width = this.addEditForm.uploadHeight;
            el.style.height = this.addEditForm.uploadHeight;
            el.style.borderWidth = this.addEditForm.uploadBorderWidth;
            el.style.borderStyle = this.addEditForm.uploadBorderStyle;
            el.style.borderColor = this.addEditForm.uploadBorderColor;
            el.style.borderRadius = this.addEditForm.uploadBorderRadius;
            el.style.backgroundColor = this.addEditForm.uploadBgColor;
          });
        document
          .querySelectorAll(".addEdit-block .upload .el-form-item__label")
          .forEach((el) => {
            el.style.lineHeight = this.addEditForm.uploadHeight;
            el.style.color = this.addEditForm.uploadLableColor;
            el.style.fontSize = this.addEditForm.uploadLableFontSize;
          });
        document
          .querySelectorAll(".addEdit-block .upload .el-icon-plus")
          .forEach((el) => {
            el.style.color = this.addEditForm.uploadIconFontColor;
            el.style.fontSize = this.addEditForm.uploadIconFontSize;
            el.style.lineHeight = iconLineHeight;
            el.style.display = "block";
          });
        // 多文本输入框
        document
          .querySelectorAll(".addEdit-block .textarea .el-textarea__inner")
          .forEach((el) => {
            el.style.height = this.addEditForm.textareaHeight;
            el.style.color = this.addEditForm.textareaFontColor;
            el.style.fontSize = this.addEditForm.textareaFontSize;
            el.style.borderWidth = this.addEditForm.textareaBorderWidth;
            el.style.borderStyle = this.addEditForm.textareaBorderStyle;
            el.style.borderColor = this.addEditForm.textareaBorderColor;
            el.style.borderRadius = this.addEditForm.textareaBorderRadius;
            el.style.backgroundColor = this.addEditForm.textareaBgColor;
          });
        document
          .querySelectorAll(".addEdit-block .textarea .el-form-item__label")
          .forEach((el) => {
            // el.style.lineHeight = this.addEditForm.textareaHeight
            el.style.color = this.addEditForm.textareaLableColor;
            el.style.fontSize = this.addEditForm.textareaLableFontSize;
          });
        // 保存
        document
          .querySelectorAll(".addEdit-block .btn .btn-success")
          .forEach((el) => {
            el.style.width = this.addEditForm.btnSaveWidth;
            el.style.height = this.addEditForm.btnSaveHeight;
            el.style.color = this.addEditForm.btnSaveFontColor;
            el.style.fontSize = this.addEditForm.btnSaveFontSize;
            el.style.borderWidth = this.addEditForm.btnSaveBorderWidth;
            el.style.borderStyle = this.addEditForm.btnSaveBorderStyle;
            el.style.borderColor = this.addEditForm.btnSaveBorderColor;
            el.style.borderRadius = this.addEditForm.btnSaveBorderRadius;
            el.style.backgroundColor = this.addEditForm.btnSaveBgColor;
          });
        // 返回
        document
          .querySelectorAll(".addEdit-block .btn .btn-close")
          .forEach((el) => {
            el.style.width = this.addEditForm.btnCancelWidth;
            el.style.height = this.addEditForm.btnCancelHeight;
            el.style.color = this.addEditForm.btnCancelFontColor;
            el.style.fontSize = this.addEditForm.btnCancelFontSize;
            el.style.borderWidth = this.addEditForm.btnCancelBorderWidth;
            el.style.borderStyle = this.addEditForm.btnCancelBorderStyle;
            el.style.borderColor = this.addEditForm.btnCancelBorderColor;
            el.style.borderRadius = this.addEditForm.btnCancelBorderRadius;
            el.style.backgroundColor = this.addEditForm.btnCancelBgColor;
          });
      });
    },
    addEditUploadStyleChange() {
      this.$nextTick(() => {
        document
          .querySelectorAll(
            ".addEdit-block .upload .el-upload-list--picture-card .el-upload-list__item"
          )
          .forEach((el) => {
            el.style.width = this.addEditForm.uploadHeight;
            el.style.height = this.addEditForm.uploadHeight;
            el.style.borderWidth = this.addEditForm.uploadBorderWidth;
            el.style.borderStyle = this.addEditForm.uploadBorderStyle;
            el.style.borderColor = this.addEditForm.uploadBorderColor;
            el.style.borderRadius = this.addEditForm.uploadBorderRadius;
            el.style.backgroundColor = this.addEditForm.uploadBgColor;
          });
      });
    },
    // 得到区域信息
    async getAreaList() {
      const { data: res } = await this.$http({
        url: '/area/list',
        method: 'get'
      })
      if(res.code != 0)return console.log('请求区域信息失败')
      this.areaList = res.data
    },
    // 得到楼栋
    async areaChangeHandle(areaId) {
      const { data: res } = await this.$http({
        url: '/floor/list',
        method: 'get',
        params: {
          areaId
        }
      })
      if(res.code != 0) return console.log('请求楼栋信息失败')
      this.floorList = res.data
    },
    // 得到房间列表 
    async floorChangeHandle(floorId) {
      const { data: res } = await this.$http({
        url: '/room/list',
        method: 'get',
        params: {
          floorId
        }
      })
      if(res.code != 0) return console.log('请求房间信息失败')
      this.roomList = res.data
    }
  },
};
</script>
