<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
      style="background: transparent"
    >
      <el-row>
        <el-col :span="12">
          <el-form-item label="用户名" prop="username">
            <el-input
              v-model="ruleForm.username"
              placeholder="用户名"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="用户类型"  prop="role">
            <el-input
              v-model="ruleForm.role"
              placeholder="用户类型"
              disabled
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="手机号"  prop="phone">
            <el-input
              v-model="ruleForm.phone"
              placeholder="手机号"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
        <!-- <el-col :span="12">
          <el-form-item v-if="flag == 'yonghu'" label="性别" prop="xingbie">
            <el-select v-model="ruleForm.xingbie" placeholder="请选择性别">
              <el-option
                v-for="(item, index) in yonghuxingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-col> -->
        <el-row>
        <el-form-item> </el-form-item>
        <el-col :span="24">
          <el-form-item>
            <el-button type="primary" @click="onUpdateHandler">修 改</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
// 数字，邮件，手机，url，身份证校验
import {
  isNumber,
  isIntNumer,
  isEmail,
  isMobile,
  isPhone,
  isURL,
  checkIdCard,
} from "@/utils/validate";

export default {
  data() {
    return {
      ruleForm: {},
      flag: "",
      usersFlag: false,
      yonghuxingbieOptions: [],
    };
  },
  mounted() {
    var table = this.$storage.get("sessionTable");
    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get",
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
    this.yonghuxingbieOptions = "男,女".split(",");
  },
  methods: {
    yonghutouxiangUploadChange(fileUrls) {
      this.ruleForm.touxiang = fileUrls;
    },
    onUpdateHandler() {
      if (!this.ruleForm.yonghuming && "yonghu" == this.flag) {
        this.$message.error("用户名不能为空");
        return;
      }
      if (!this.ruleForm.mima && "yonghu" == this.flag) {
        this.$message.error("密码不能为空");
        return;
      }
      if (!this.ruleForm.xingming && "yonghu" == this.flag) {
        this.$message.error("姓名不能为空");
        return;
      }
      if (this.ruleForm.touxiang != null) {
        this.ruleForm.touxiang = this.ruleForm.touxiang.replace(
          new RegExp(this.$base.url, "g"),
          ""
        );
      }
      if (
        "yonghu" == this.flag &&
        this.ruleForm.shouji &&
        !isMobile(this.ruleForm.shouji)
      ) {
        this.$message.error(`手机应输入手机格式`);
        return;
      }
      if ("users" == this.flag && this.ruleForm.username.trim().length < 1) {
        this.$message.error(`用户名不能为空`);
        return;
      }
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm,
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "修改信息成功",
            type: "success",
            duration: 1500,
            onClose: () => {},
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    },
  },
};
</script>
<style lang="scss" scoped>
</style>
